<template>
  <div id="demo-editor">
    <el-dialog title="" v-model="dialogVisible" v-on:close="handleClose()">
      <el-form :model="ruleForm" :rules="rules" ref="ruleForm" label-width="100px">
        <el-form-item label="" prop="name">
          <el-input v-model="ruleForm.name"></el-input>
        </el-form-item>
        <el-form-item label="" prop="region">
          <el-select v-model="ruleForm.region" placeholder="">
            <el-option label="" value="shanghai"></el-option>
            <el-option label="" value="beijing"></el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="" required>
          <el-col :span="11">
            <el-form-item prop="date1">
              <el-date-picker type="date" placeholder="" v-model="ruleForm.date1" style="width: 100%;"></el-date-picker>
            </el-form-item>
          </el-col>
          <el-col class="line" :span="2">-</el-col>
          <el-col :span="11">
            <el-form-item prop="date2">
              <el-time-picker type="fixed-time" placeholder="" v-model="ruleForm.date2" style="width: 100%;"></el-time-picker>
            </el-form-item>
          </el-col>
        </el-form-item>
        <el-form-item label="">
          <el-switch on-text="" off-text="" v-model="ruleForm.delivery"></el-switch>
        </el-form-item>
        <el-form-item label="" prop="type">
          <el-checkbox-group v-model="ruleForm.type">
            <el-checkbox label="/" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
            <el-checkbox label="" name="type"></el-checkbox>
          </el-checkbox-group>
        </el-form-item>
        <el-form-item label="" prop="resource">
          <el-radio-group v-model="ruleForm.resource">
            <el-radio label=""></el-radio>
            <el-radio label=""></el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item label="" prop="desc">
          <el-input type="textarea" v-model="ruleForm.desc"></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose()"> </el-button>
        <el-button type="primary" :loading="btnLoading" @click="handleSubmit()"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import DemoStore from '../DemoStore'
import DemoService from '../DemoService'

export default {
  props: {
    formLabelWidth: {
      type: String,
      required: false,
      default: '120px'
    },
    dialogVisible: {
      type: Boolean,
      required: true,
      default: false
    },
    tableRow: {
      type: Object,
      required: false,
      default: {}
    }
  },
  data() {
    return {
      target: this.tableRow,
      btnLoading: false,
      demoService: new DemoService(this),
      ruleForm: {
        name: '',
        region: '',
        date1: '',
        date2: '',
        delivery: false,
        type: [],
        resource: '',
        desc: ''
      },
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 5, message: ' 3  5 ', trigger: 'blur' }
        ],
        region: [
          { required: true, message: '', trigger: 'change' }
        ],
        date1: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ],
        date2: [
          { type: 'date', required: true, message: '', trigger: 'change' }
        ],
        type: [
          { type: 'array', required: true, message: '', trigger: 'change' }
        ],
        resource: [
          { required: true, message: '', trigger: 'change' }
        ],
        desc: [
          { required: true, message: '', trigger: 'blur' }
        ]
      }
    };
  },
  methods: {
    handleClose () {
      this.$refs.ruleForm.resetFields()
      this.$emit('closeDialog')
    },
    handleSubmit(ev) {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.btnLoading = true
          if (this.target.id) {
            this.updateDemo()
          } else {
            this.createDemo()
          }
        } else {
          return false;
        }
      });
    },
    createDemo () {
      this.demoService.create('/admin/demo/save').then((resp) => {
        if (resp.success) {
          this.$emit('closeDialog')
        } else {
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        }
      }).catch((resp) => {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        })
      }).finally(() => {
        this.btnLoading = false
      })
    },
    updateDemo () {
      this.demoService.update('/admin/demo/update').then((resp) => {
        if (resp.success) {
          this.$emit('closeDialog')
        } else {
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        }
      }).catch((resp) => {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        })
      }).finally(() => {
        this.btnLoading = false
      })
    }
  }
}
</script>



