<template>
  <div id="user-editor">
    <el-dialog title="" v-model="dialogVisible" v-on:close="handleClose()">
      <el-form :model="target" :rules="rules" ref="ruleForm" :label-width="formLabelWidth">
        <el-form-item label="" prop="name">
          <el-input v-model="target.name" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="password">
          <el-input type="password" v-model="target.password" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="repassword">
          <el-input type="password" v-model="target.repassword" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="email">
          <el-input v-model="target.email" placeholder=""></el-input>
        </el-form-item>
        <el-form-item label="" prop="tel">
          <el-input v-model="target.tel" placeholder=""></el-input>
        </el-form-item>
      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button @click="handleClose()"> </el-button>
        <el-button type="primary" :loading="btnLoading" @click="handleSubmit()"></el-button>
      </div>
    </el-dialog>
  </div>
</template>
<script>
import UserStore from '../UserStore'
import UserService from '../UserService'
import Regular from 'src/common/regular'

export default {
  props: {
    formLabelWidth: {
      type: String,
      required: false,
      default: '120px'
    },
    dialogVisible: {
      type: Boolean,
      required: true,
      default: false
    },
    tableRow: {
      type: Object,
      required: false,
      default: {}
    }
  },
  data() {
    const validatePassword = (rule, value, callback) => {
      if (value !== this.target.password) {
        callback(new Error('!'))
      } else {
        callback()
      }
    }

    const validateTel = (rule, value, callback) => {
      if (!Regular.tel.test(value)) {
        callback(new Error(''));
      } else {
        callback()
      }
    }
    return {
      target: {
        name: '',
        password: '',
        repassword: '',
        email: '',
        tel: ''
      },
      btnLoading: false,
      userService: new UserService(this),
      rules: {
        name: [
          { required: true, message: '', trigger: 'blur' },
          { min: 3, max: 20, message: ' 3  20 ', trigger: 'blur' }
        ],
        password: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' }
        ],
        repassword: [
          { required: true, message: '', trigger: 'blur' },
          { min: 6, max: 20, message: ' 6  20 ', trigger: 'blur' },
          { validator: validatePassword, trigger: 'blur' }
        ],
        email: [
          { required: true, message: '', trigger: 'blur' },
          { type: 'email', message: '', trigger: 'blur,change' }
        ],
        tel: [
          { required: true, message: '', trigger: 'blur' },
          { min: 11, max: 11, message: '11', trigger: 'blur' },
          { validator: validateTel, trigger: 'blur,change' }
        ],
      }
    };
  },
  watch: {
    tableRow: function (val) {
      Object.assign(this.target, val)
    }
  },
  methods: {
    handleClose () {
      this.$refs.ruleForm.resetFields()
      this.$emit('closeDialog')
    },
    handleSubmit(ev) {
      this.$refs.ruleForm.validate((valid) => {
        if (valid) {
          this.btnLoading = true
          if (this.target.id) {
            this.updateUser()
          } else {
            this.createUser()
          }
        } else {
          return false;
        }
      });
    },
    createUser () {
      this.userService.create('/user/save').then((resp) => {
        if (resp.success) {
          this.$emit('closeDialog', 'success')
        } else {
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        }
      }).catch((resp) => {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        })
      }).finally(() => {
        this.btnLoading = false
      })
    },
    updateUser () {
      this.userService.update('/user/update').then((resp) => {
        if (resp.success) {
          this.$emit('closeDialog', 'success')
        } else {
          this.$notify({
            title: '',
            message: '',
            type: 'error'
          })
        }
      }).catch((resp) => {
        this.$notify({
          title: '',
          message: '',
          type: 'error'
        })
      }).finally(() => {
        this.btnLoading = false
      })
    }
  }
}
</script>



